import './App.css';
import React from 'react'
class App extends React.Component {

  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = { random: 0 };
  }

  handleClick() {
    let rand = Math.floor(Math.random() * 3) +1
    if (rand === 1 || rand === 2 ) {
      rand = Math.floor(Math.random() * 3) +1
    }
    this.setState({ random: rand });
  }

  render() {
    return (
      <div>
        <img src = "https://investinginfo.org/wp-content/uploads/2021/02/IMG_3156.jpg"/>
        <br/>
        <button onClick={this.handleClick.bind(this)} class="outline" type="button">
                <div class="label">
                    <span class="hover-effect"></span>
                    <span class="label-text">Generate Number</span>
                </div>
            </button>
        <h1>The number is: {this.state.random}</h1>
      </div>
    );
  }
}


export default App;
